import * as React from 'react';
import { Box } from '@chakra-ui/core';

export default function Carousel({ children }) {
  return (
    <Box position="relative">
      <Box position="relative" overflow="hidden" maxW="100%" w="100%" zIndex="1">
        <Box display="flex">
          {children.map((item) => (
            <Box flex="1" overflow="hidden">
              {item}
            </Box>
          ))}
        </Box>
      </Box>
      <div>
        <button>next</button>
        <button>prev</button>
      </div>
    </Box>
  );
}
